import React , {useState , useEffect} from 'react'
import { FaAngleDoubleRight } from "react-icons/fa";
import './style.css'
 const url = 'https://course-api.com/react-tabs-project';


function Main() {
 const [jops ,setJops] = useState([])
 const [loading , setLoading]=useState(true)
 const [value , setValue] = useState(0)
 
 const fetchJops = async()=>{
  setLoading(true)
  const respons = await fetch(url);
  let data = await respons.json();
  setJops(data)
  setLoading(false)
 }
 useEffect(()=>{
  fetchJops()
  document.title='Tabs'
 },[])
 console.log(jops[0])

 if(loading){
  return(
   <div className="container2">
    <h1>Loading...</h1>
   </div>
  )
 }
const { company , dates , duties , title } = jops[value];
  return (
  <main>
   <div className="container">
    <div className="header">
     <h1>Experience</h1>
     <div className='theLine'></div>
    </div>

    <section>
     <div className="mainContent">
      <div className="buttons">
       {/* ///////// */}
       {/* <button className='btn activebtn'>{company}</button> */}
       {
        jops.map((button , index)=>{
         const {company} = button
          return(
           <button key={index} className={`btn ${index === value && ' activebtn'}`} onClick={()=>setValue(index)}>{company}</button>
          )
        })
       }
       {/* ///////// */}
      </div>
      <div className="article">
       <h2 className='jopHeader'>{title}</h2>
       <h3>{company}</h3>
       <p className='date'>{dates}</p>
       <div className="roles">
        {duties.map((duty , index)=>{
          return(
            <div className="role" key={index}>
           < FaAngleDoubleRight className='roleico'/>
           <p>{duty}</p>
         </div>
          )
        })}
       </div>
      </div>
     </div>
     <div className="moreinfo">
      <button className='btn2'>More Info</button>
     </div>
    </section>
   </div>
  </main>
 )

}

export default Main
